@mixin font-face($font-family, $file-path, $weight: normal, $style: normal) {
    @font-face {
        font-family: $font-family;
        font-weight: $weight;
        font-style: $style;
        src: url("#{$file-path}.eot");
        src: url("#{$file-path}.eot?#iefix") format("embedded-opentype"),
            url("#{$file-path}.woff") format("woff"),
            url("#{$file-path}.ttf") format("truetype");
        font-display: swap;
    }
}

@include font-face(
    "Raleway",
    "../fonts/Raleway/Raleway-Light/Raleway-Light",
    300
);

@include font-face(
    "Raleway",
    "../fonts/Raleway/Raleway-Regular/Raleway-Regular",
    400
);

@include font-face(
    "Raleway",
    "../fonts/Raleway/Raleway-Medium/Raleway-Medium",
    500
);

@include font-face(
    "Raleway",
    "../fonts/Raleway/Raleway-SemiBold/Raleway-SemiBold",
    600
);
